import React, { useEffect, useState } from "react";
import Carousel from "react-slick";
import "./css/BlogSection.scss";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import getApiUrl from "../../helper/helper";
export interface IBlog {
  id: number;
  title: string;
  description: string;
  sub_description: string;
  sub_sub_description: string;
  file_type: string;
  file_name: string;
}
const BlogSection: React.FC = () => {
  const newsItems = [
    {
      id: 1,
      title: "Fremtidens Webdesign  Hva Kan Vi Forvente?",
      content:
        "Webdesign str aldri stille, og med teknologiens raske utvikling er det spennende  spekulere i hva fremtiden bringer. Fra AI-drevne design til fullstendig immersive opplevelser, utforsker vi de nye trendene som vil forme vr digitale verden. Vil virtuell og utvidet virkelighet bli en standard for online interaksjon? Hvordan vil brukeropplevelsen utvikle seg med mer personlige og tilpasningsdyktige nettsider? Bli med oss for en dypdykk i webdesignens fremtidige landskap.",
      image: "/news.png",
    },
    {
      id: 2,
      title: "Brekraft i Digitalt Design  Mer enn Bare en Trend",
      content:
        "Brekraft blir stadig viktigere, ikke bare i den fysiske verden, men ogs i digitalt design. Fra  redusere karbonavtrykket av nettsider til etisk design som fremmer inkludering og tilgjengelighet  vi utforsker hvordan designere kan bidra til en mer brekraftig og rettferdig digital fremtid. Oppdag hvordan sm valg i designprosessen kan gjre en stor forskjell for planeten vr.",
      image: "/news1.png",
    },
    {
      id: 3,
      title: "Den kende Rollen av UX i Kundens Reise Online",
      content:
        "Brukeropplevelse (UX) er mer kritisk enn noen gang i  forme kundens reise p nettet. Fra det frste klikket til det endelige kjpet, spiller UX en nkkelrolle i  konvertere beskende til lojale kunder. Vi ser p hvordan bedrifter kan optimalisere sin online tilstedevrelse ved  fokusere p smidige, intuitive og engasjerende brukeropplevelser.",
      image: "/news2.png",
    },
  ];
  const [blogs, setBlogs] = useState<IBlog[]>([]);
  const [loading, setLoading] = useState(true);

  const handleSuccess = () => {
    fetch(`${getApiUrl()}/blogs`)
      .then((response) => response.json())
      .then((data) => {
        setBlogs(data);
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
        console.error("Error fetching price data:", error);
      });
  };
  useEffect(() => {
    handleSuccess();
  }, []);

  const [slidesToShow, setSlidesToShow] = useState(1);

  useEffect(() => {
    const handleResize = () => {
      const width = window.innerWidth;
      if (width > 768) {
        if (newsItems.length >= 3) {
          setSlidesToShow(3);
        } else {
          setSlidesToShow(newsItems.length);
        }
      } else {
        setSlidesToShow(1);
      }
    };

    handleResize();
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, [newsItems.length]);

  const settings = {
    infinite: newsItems.length > slidesToShow, // Aktiverer infinite bare nr det er flere kort enn slidesToShow
    speed: 600,
    slidesToShow: slidesToShow,
    slidesToScroll: 1,
    autoplay: newsItems.length > 1, // Aktiverer autoplay bare nr det er mer enn en omtale
    autoplaySpeed: 2500,
  };

  if (loading) {
    return <div>Loading...</div>;
  }
  return (
    <div className="news-section">
      <div className="news-section__container">
        <div className="news-section-header">
          <h2 className="news-section-title">Utforsk vr blogg</h2>
          <p className="news-section-description">
            Velkommen til vr blogg! Her kan du utforske et mangfold av emner,
            fra de siste trendene innen teknologi og vitenskap til praktiske
            tips og triks for hverdagen. Bli med oss p en reise gjennom
            inspirerende historier, dyptplyende analyser og meningsfulle
            diskusjoner som vil utvide horisontene dine og gi deg verdifull
            innsikt.
          </p>
        </div>
        <div className="news-carousel">
          <Carousel {...settings}>
            {blogs?.map((blog: IBlog, index) => (
              <div
                key={index}
                className="news-card"
                onClick={() => {
                  window.location.href = `/blog?id=${blog.id}`;
                }}
              >
                <img
                  src={`./${blog.file_name}`}
                  alt={blog.title}
                  className="news-image"
                />
                <h3 className="news-title">{blog.title}</h3>
                <p className="news-content">{blog.description}</p>
                <button
                  className="card-button-read-more"
                  onClick={() => (window.location.href = "/blog?id=1")}
                >
                  Les mer
                </button>
              </div>
            ))}
          </Carousel>
        </div>
      </div>
    </div>
  );
};

export default BlogSection;
